@import "layouts";
@import "block/other/left-side";
@import "block/other/news";
@import "block/other/catalog";
@import "block/other/filter";
@import "block/product";

.page {
    margin-bottom: 2rem;
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
    &__right {
        width: 100%;
        margin-top: 1rem;
        @include rmin($sm) {
            width: calc(100% - 16.375rem);
            margin-top: -0.25rem; } }
    &__social-btn {
        padding-top: 1.5rem;
        margin-top: 1.5rem;
        border-top: 1px solid #cccccc; } }
